.sortOptions{
    padding: 4rem;
    display: grid;
    min-width: 400px;

    gap: 3rem;  
}
.sortOptions__box{
    display: grid;
}

/* -------------------------------- */

.sortOptions__box label{
    font-size: 2rem;
    padding: 1ch 0;
}
.rangeInput{
    --height: 1.8rem;
    
    -webkit-appearance: none; 
    appearance: none;
    outline: none; 

    background: #fff;
    margin: .5rem 0;
    
    height: var(--height);
    
    border-radius: var(--height);
    box-shadow: 0px 0px .8rem rgba(0,0,0,.2);
}
.rangeInput::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    
    width: var(--height);
    height: var(--height);
    background: var(--dark_blue__light);
    cursor: pointer;

    border-radius: var(--height);
}
.rangeInput::-moz-range-thumb {
    width: 25px; 
    height: 25px; 
    background: var(--dark_blue__light);
    cursor: pointer;
}

/* -------------------------------- */

.sortOptions__select{
    padding: 1.3ch;
    font-size: 1.4rem;
}

/* -------------------------------- */
.sortOptions__btn{
    border: none;
    background-color: var(--dark_blue);

    font-size: 1.7rem;
    padding: 1.3ch;

    color: #fff;
    border-radius: 5rem;
    
    transition-duration: .3s;
}
.sortOptions__btn:hover{
    cursor: pointer;
    background-color: var(--dark_blue__light)
}