.sortBoard{
    padding: 5rem;
    /* width: 800px; */
    height: 85vh;

    display: grid;
    align-items: end;
}
.sortBoard__element{
    background-color: var(--light_blue);
    width: 100%;

    transition-duration: .1s;
    /* transition-timing-function: cubic-bezier(0.2, 1.03, 0.37, 1.02); */
    transition-property: height;
}
.--active{
    background-color: var(--dark_blue);
}
.--final{
    background-color: var(--dark_blue);
}