:root{
    font-family: 'Ubuntu', sans-serif;

    --bg: #f5ebe0;
    --bg_alt: #e3d5ca;

    --dark_blue: #004869;
    --dark_blue__light: #0070a3;
    --light_blue: #468bac;


    /* --c3:#468bac;
    --c4:rgb(0, 0, 0);
    --c5:#004869; */
}

.app{
    width: 100vw;
    height: 100vh;

    background-color: #fff;
    /* background-color: #000000; */

    display: grid;
    grid-template-rows: auto 1fr;
}
/*  --------- Navigation --------- */
nav{
    z-index: 1000;

    width: 100%;
    color: #fff;
    height: 5rem;
    background-color: #004869;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3ch;
}
.nav__el{
    font-size: 2.2rem;
    letter-spacing: .05ch;
    text-decoration: none;
    color: inherit;

    transition-duration: .3s;
}
.nav__el:hover{
    text-shadow: 0 0 1rem rgba(0, 0, 0, .4);
}

main{
    width: 100%;
    display: grid;
    grid-template-columns: 1fr auto;
}

main > section{
    width: 100%;
    height: 100%;
}

.visualSection{
    /* background-color: var(--c1); */
    background-color: #fff;
}
.settingsSection{
    /* background-color: var(--c2); */
    background-color: #fff;
    box-shadow: 0 0 1rem rgba(0, 0, 0, .4);
}